.hos-login-Form {
  position: relative;
  width: 500px;
  height: 450px;
  left: 50%;
  transform: translateX(-50%);
  margin: 0;
  top: 150px;
  padding: 30px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.95);
}
.hos-login-Form .title {
  position: relative;
  width: 100%;
  text-align: center;
  top: 20px;
  color: #383737;
  font-size: 35px;
  font-weight: bold;
  margin-bottom: 40px;
}
.hos-login-Form .title .icon {
  position: relative;
  height: 100px;
  width: 100px;
  margin-top: -10px;
}
