.tips-container {
  position: relative;
  height: 100%;
  width: 100%;
}
.tips-container .tips-title {
  font-size: 30px;
  margin-top: -10px;
  transform: rotate(-5deg);
  color: #eb3819;
}
.tips-container .split-line {
  margin: 25px 0 0px 0;
  border: 1px solid lightseagreen;
}
.tips-container .tips-body {
  position: relative;
  height: calc(100% - 50px);
  width: 100%;
  background: #616060;
}
