.hospitals-container {
  position: relative;
  height: 100%;
  width: 100%;
}
.hospitals-container .title {
  font-size: 18px;
  color: #1c3461;
  margin-left: 30px;
  margin-right: 200px;
}
.hospitals-container .card-region {
  white-space: pre-wrap;
  padding-top: 30px;
}
.hospitals-container .card-region .card {
  display: inline-block;
  height: 210px;
  width: 230px;
  margin: 0px 20px 50px 30px;
  background: white;
  font-size: 16px;
  color: #141313;
  text-align: center;
}
.hospitals-container .card-region .card img {
  height: 210px;
  width: 230px;
}
.hospitals-container .card-region .card div {
  position: absolute;
  padding: 0;
}
.hospitals-container .card-region .card div a {
  color: #0004ff;
}
.hospitals-container .card-region .card div p {
  position: absolute;
  background: rgba(243, 244, 245, 0.9);
  color: #080808;
  padding: 5px;
  transform: translateY(-100%);
  white-space: nowrap;
  overflow-x: auto;
  border-radius: 5px;
}
.hospitals-container .card-region .card div p span {
  margin-left: 20px;
}
