.second-container {
  position: relative;
  height: 750px;
  width: 100%;
  overflow-y: auto;
}
.second-container .form {
  border-bottom: 2px solid gray;
}
.second-container .form select {
  width: 150px;
  margin-top: 20px;
}
.second-container .form label {
  line-height: 70px;
  text-align: right;
}
.second-container .doctor-info {
  position: relative;
  width: 100%;
  overflow-y: auto;
}
.second-container .doctor-info .card {
  position: relative;
  height: 300px;
  width: 300px;
  text-align: center;
  margin-top: 10px;
  margin-right: 10px;
  display: inline-block;
  vertical-align: top;
}
.second-container .doctor-info .card img {
  height: 160px;
  width: 140px;
}
.second-container .doctor-info .card .card-body {
  padding: 5px 5px 0 5px;
}
.second-container .doctor-info .card .card-body .card-title {
  margin-bottom: 5px;
}
.second-container .doctor-info .card .card-body .card-text {
  height: 80px;
  overflow-y: auto;
}
