.firstPage-container {
  position: relative;
  height: 100vh;
  width: 1200px;
  left: 50%;
  transform: translateX(-50%);
}
.firstPage-container .leftContainer {
  display: inline-block;
  vertical-align: top;
  height: 100%;
  width: 50%;
}
.firstPage-container .leftContainer .announcement {
  position: absolute;
  height: 350px;
  width: 600px;
  margin: 50px 0 50px 0;
  border-radius: 10px;
  box-shadow: 0 0 2px #cccbcb;
  background-color: #c6f6fa;
}
.firstPage-container .leftContainer .healthyTips {
  position: absolute;
  height: 350px;
  width: 600px;
  top: 450px;
  border-radius: 10px;
  box-shadow: 0 0 1px gray;
  background: white;
}
.firstPage-container .loginContainer {
  display: inline-block;
  vertical-align: top;
  position: relative;
  margin-top: 15%;
  left: 25%;
  transform: translateX(-50%);
  height: 500px;
  width: 450px;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 0 2px #c7c5c5;
  background: linear-gradient(#d4f1fa, #fdfaef);
}
