.history-container {
  position: relative;
  height: 100%;
  width: 100%;
}
.history-container .title {
  position: relative;
  height: 50px;
  width: 100%;
  border-bottom: 3px solid lightseagreen;
  border-radius: 2px;
  font-size: 25px;
  text-align: center;
  line-height: 50px;
  color: #f15416;
}
.history-container .body {
  position: relative;
  height: calc(100% - 50px);
  width: 100%;
  overflow: auto;
}
.history-container .body .card {
  height: 180px;
  width: 200px;
  margin: 10px 0 50px 0;
  left: 50%;
  margin-left: -100px;
  background: lightgray;
  font-size: 16px;
  color: #141313;
  text-align: center;
}
.history-container .body .card img {
  height: 180px;
  width: 200px;
}
.history-container .body .card div {
  position: relative;
  padding: 0;
}
.history-container .body .card div p {
  position: absolute;
  background: rgba(243, 244, 245, 0.9);
  color: #080808;
  padding: 5px;
  transform: translateY(-100%);
  white-space: nowrap;
  overflow-x: auto;
  border-radius: 5px;
}
