.update-header {
  position: relative;
  width: 100%;
  height: 90px;
}
.update-header .form {
  border-bottom: 2px solid gray;
  text-align: center;
  padding-left: 50px;
}
.update-header .form .form-group {
  margin: 0;
}
.update-header .form select {
  display: inline;
  width: 120px;
  margin-top: 25px;
}
.update-header .form label {
  line-height: 80px;
  text-align: right;
}
.update-body {
  position: relative;
  width: 100%;
  height: calc(100vh - 210px);
  overflow-y: auto;
  text-align: center;
}
.update-body th,
.update-body td {
  text-align: center;
}
.update-body td:not(:nth-child(4)) {
  padding-top: 45px;
}
.update-body .inner-table td {
  padding-top: 0;
}
