.login-Form {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
.login-Form .title {
  position: relative;
  width: 100%;
  text-align: center;
  top: -40px;
  color: #383737;
  font-size: 35px;
  font-weight: bold;
  margin-bottom: -10px;
}
.login-Form .title .icon {
  position: relative;
  height: 100px;
  width: 100px;
  margin-top: -10px;
}
.login-Form .btn-pwFind {
  display: inline-block;
  vertical-align: top;
  margin: 10px 0 0 100px;
  padding: 7px;
}
.login-Form .btn-register {
  margin: 10px 0px 0 0px;
}
