.hos-upload-container {
  position: relative;
  height: 100%;
  width: 100%;
  text-align: center;
  overflow-y: auto;
}
.hos-upload-container form {
  position: relative;
  width: 700px;
  margin-top: 30px;
  left: 50%;
  transform: translateX(-50%);
}
.hos-upload-container form #hosUpdateImg {
  height: 150px;
  width: 200px;
  padding: 0;
  margin-bottom: 10px;
}
.hos-upload-container form #hosUpdateImgSelect {
  position: relative;
  background: white;
}
.hos-upload-container form #imgSelect:focus {
  outline: none;
}
