.doctor-view-header {
  position: relative;
  width: 100%;
  height: 90px;
}
.doctor-view-header .form {
  border-bottom: 2px solid gray;
  text-align: center;
  padding-left: 350px;
}
.doctor-view-header .form .form-group {
  margin: 0;
}
.doctor-view-header .form select {
  display: inline;
  width: 95px;
  margin-top: 20px;
}
.doctor-view-header .form label {
  line-height: 80px;
  text-align: right;
}
.doctor-view-header .form .view-choose-btn {
  margin: 20px 0 0 30px;
}
.doctor-view-body {
  position: relative;
  width: 100%;
  height: calc(100vh - 210px);
  overflow-y: auto;
  text-align: center;
}
.doctor-view-body .total-to-detail {
  display: block;
  margin: 10px 0 10px 0;
}
.doctor-view-body th,
.doctor-view-body td {
  text-align: center;
}
.doctor-view-body td:nth-child(4) {
  color: #0051ff;
}
.doctor-view-body td:nth-child(5) {
  color: #ff4800;
}
