.reservation-finished-container {
  position: relative;
  height: 100%;
  width: 100%;
  padding-top: 10px;
  overflow-y: auto;
}
.reservation-finished-container th {
  text-align: center;
}
.reservation-finished-container td {
  max-width: 300px;
  overflow: auto;
  text-align: center;
}
.reservation-finished-container td:not(:last-child) {
  padding-top: 60px;
}
