.hos-info-update-container {
  position: relative;
  height: 100%;
  width: 100%;
  text-align: center;
  overflow-y: auto;
}
.hos-info-update-container form {
  position: relative;
  width: 700px;
  margin-top: 30px;
  left: 50%;
  transform: translateX(-50%);
}
.hos-info-update-container form #img {
  height: 150px;
  width: 200px;
  padding: 0;
  margin-bottom: 10px;
}
.hos-info-update-container form #imgSelect {
  position: relative;
  background: white;
}
.hos-info-update-container form #imgSelect:focus {
  outline: none;
}
