.health-detail-container {
  position: relative;
  height: 100vh;
  width: 100vw;
}
.health-detail-container .header .to-login {
  font-size: 18px;
  margin-left: 450px;
}
.health-detail-container .body {
  position: relative;
  width: 1000px;
  height: calc(100% - 80px);
  left: 50%;
  transform: translateX(-50%);
  box-sizing: content-box;
}
.health-detail-container .body .card {
  height: 100%;
  width: 1000px;
  background: rgba(252, 252, 252, 0.9);
}
.health-detail-container .body .card .card-header {
  font-size: 22px;
  font-weight: bold;
  color: #155861;
}
.health-detail-container .body .card .card-body {
  overflow-y: auto;
}
.health-detail-container .body .card .card-text {
  text-indent: 2em;
}
