.header {
  position: relative;
  height: 80px;
  width: 100%;
  background: rgba(252, 252, 252, 0.8);
  border-bottom: 3px solid lightskyblue;
  white-space: nowrap;
}
.header img {
  height: 80px;
  width: 80px;
  margin-left: 150px;
  margin-top: -15px;
}
.header .website-name {
  display: inline-block;
  height: 60px;
  width: 250px;
  font-size: 33px;
  margin-top: 10px;
  margin-left: 10px;
  line-height: 60px;
  border-right: 3px solid #0c0b0b;
}
.header .content {
  display: inline-block;
  height: 80px;
  width: 50px;
  font-size: 25px;
  line-height: 80px;
  margin-left: 20px;
  font-weight: bold;
  color: #e26c1e;
}
