.pw-forget-container {
  position: relative;
  height: 100%;
  width: 100%;
}
.pw-forget-container .info-Form {
  width: 600px;
}
.pw-forget-container .info-Form .title {
  width: 100%;
  text-align: center;
}
.pw-forget-container .info-Form .select-btn button {
  background: white;
  color: black;
}
.pw-forget-container .info-Form .select-btn .dropdown-menu {
  max-height: 200px;
  overflow-y: scroll;
}
.pw-forget-container .info-Form .select-btn .dropdown-item:hover {
  background: lightgray;
}
