.first-container {
  position: relative;
  height: 100%;
  width: 100%;
  overflow-y: auto;
}
.first-container .reservation-choose {
  position: relative;
  width: 100%;
  padding: 30px 30px 30px 30px;
  margin-top: 10px;
  line-height: 80px;
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
  border-radius: 5px;
  box-shadow: 0 0 1px gray;
}
.first-container .reservation-choose .form select {
  display: inline;
  width: 95px;
}
.first-container .reservation-choose .form .select-btn button {
  background: white;
  color: black;
}
.first-container .reservation-choose .form .select-btn .dropdown-menu {
  max-height: 200px;
  overflow-y: auto;
}
.first-container .reservation-choose .form .select-btn .dropdown-item {
  height: 30px;
  line-height: 30px;
}
.first-container .reservation-choose .form .select-btn .dropdown-item:hover {
  background: lightgray;
}
.first-container .reservation-choose .form label {
  line-height: 70px;
  text-align: right;
}
