.consultation-container {
  position: relative;
  height: 100%;
  width: 100%;
  overflow-y: auto;
}
.consultation-container .form {
  border-bottom: 2px solid gray;
  text-align: center;
}
.consultation-container .form .form-group {
  margin: 0;
}
.consultation-container .form select {
  display: inline;
  margin-top: 20px;
}
.consultation-container .form label {
  line-height: 70px;
  text-align: right;
}
.consultation-container .chat-container {
  position: relative;
  height: 650px;
  width: 950px;
  padding: 20px;
  overflow-y: auto;
  left: 50%;
  transform: translateX(-50%);
}
.consultation-container .chat-container .left {
  display: inline-block;
  vertical-align: top;
  height: 100%;
  width: 200px;
  box-shadow: 0 0 2px gray;
  background: white;
  border: 1px solid lightblue;
  border-radius: 10px;
}
.consultation-container .chat-container .left p {
  width: 100%;
  height: 30px;
  margin: 10px 0 0 0;
  text-align: center;
  font-size: 18px;
  border: 1px solid #f3f2f2;
  box-shadow: 0 0 1px gray;
  border-radius: 5px;
}
.consultation-container .chat-container .left p:hover {
  background: #2fe7e7;
  color: white;
}
.consultation-container .chat-container .left .title {
  font-size: 20px;
  color: #ff7b00;
  border: 0;
  border-radius: none;
  box-shadow: none;
  border-bottom: 2px solid #25a9dd;
}
.consultation-container .chat-container .left .title:hover {
  background: white;
  color: #ff7b00;
}
.consultation-container .chat-container .right {
  display: inline-block;
  vertical-align: top;
  height: 100%;
  width: 700px;
  box-shadow: 0 0 2px gray;
  background: white;
  border: 1px solid lightblue;
  border-radius: 10px;
}
.consultation-container .chat-container .right .chat-body {
  position: relative;
  padding-top: 5px;
  width: 100%;
  height: 500px;
  border-radius: 10px;
  overflow-y: auto;
  background: #f5f4f4;
}
.consultation-container .chat-container .right .chat-body p {
  position: relative;
  margin: 15px 0 0px 10px;
}
.consultation-container .chat-container .right .chat-body p span {
  padding: 7px;
  background: linear-gradient(#f7f8f8, #17c4fd);
  border-radius: 5px;
}
.consultation-container .chat-container .right .chat-bottom {
  position: relative;
  width: 100%;
  height: calc(100% - 500px);
  box-shadow: 0 0 3px gray;
}
.consultation-container .chat-container .right .chat-bottom .input-area {
  border-radius: 5px;
  border: 2px solid gray;
  height: 100%;
  width: 100%;
  text-indent: 2em;
}
.consultation-container .chat-container .right .chat-bottom button {
  position: absolute;
  bottom: 2px;
  right: 5px;
}
